@import url(https://fonts.googleapis.com/css2?family=Inter&display=swap);
:root{font-size:22px;font-family:"Sen", sans-serif;--title-size: 2.9rem;--main-width: 35vw;--margin-sides: 130px;--project-cols: 3;--img-slide: 15%;--margin-multiplier-projects: 2;--nav-height: 80px;overflow-x:hidden}html,body{width:100vw;overflow-x:hidden}img,svg{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}a{text-decoration:none;color:inherit;cursor:pointer}*{margin:0;padding:0;opacity:1;box-sizing:border-box}

@media only screen and (min-width: 3800px){:root{--img-slide: 25%;font-size:28px}}@media only screen and (min-width: 5000px){:root{--img-slide: 40%;font-size:33px}}@media only screen and (max-width: 1500px){:root{font-size:21px;--margin-sides: 50px}}@media only screen and (max-width: 1400px){:root{font-size:20px}}@media only screen and (max-width: 1200px){.remove-small{display:none}}@media only screen and (max-width: 900px){:root{--title-size: 2.5rem;--margin-sides: 40px}}@media only screen and (max-height: 850px){:root{font-size:19px}}@media only screen and (max-width: 700px) and (min-height: 750px){header .remove-small{display:inline;display:initial}}@media only screen and (max-width: 600px){:root{font-size:18px;--title-size: 2.2rem}}@media only screen and (max-width: 500px){:root{--margin-sides: 30px;--nav-height: 60px}}@media only screen and (max-width: 400px){:root{--margin-sides: 20px}}

nav{width:100vw;height:var(--nav-height);border-bottom:solid 1px black;padding:0 var(--margin-sides);display:flex;align-items:center;justify-content:space-between;text-transform:uppercase;transform:translateY(-100%);-webkit-animation:slide-down 0.4s ease-out forwards 0.25s;animation:slide-down 0.4s ease-out forwards 0.25s}@-webkit-keyframes slide-down{from{transform:translateY(-100%)}to{transform:translateY(0)}}@keyframes slide-down{from{transform:translateY(-100%)}to{transform:translateY(0)}}nav div.menu{display:flex;align-items:center;height:100%}nav div.menu *{margin-left:40px}nav div.menu a{display:flex;align-items:center;height:100%}@-webkit-keyframes hue-rotate{from{-webkit-filter:hue-rotate(0deg);filter:hue-rotate(0deg)}to{-webkit-filter:hue-rotate(359deg);filter:hue-rotate(359deg)}}@keyframes hue-rotate{from{-webkit-filter:hue-rotate(0deg);filter:hue-rotate(0deg)}to{-webkit-filter:hue-rotate(359deg);filter:hue-rotate(359deg)}}nav div.menu a.bmc{height:2rem;border:1px solid black;border-radius:7px;overflow:hidden}nav div.menu a.bmc img{height:100%;margin:0;-webkit-animation:hue-rotate 3s infinite;animation:hue-rotate 3s infinite}nav div.hamburger{display:none}nav div.hamburger img{height:35px;transform:translateY(2px)}@media only screen and (max-width: 700px){nav div.menu{display:none}div.hamburger{display:block !important}}

header{display:flex;height:calc(100vh - var(--nav-height));min-height:560px;min-width:320px}header div.jumbo{position:relative;height:100%;min-width:50vw}header div.jumbo img.jumbo{border-radius:15px;background-image:url(data:image/png;base64,);background-position:center;background-size:cover;position:absolute;top:50%;transform:translateY(-50%);height:70%;min-height:500px;opacity:0;-webkit-animation:slide-in forwards 1.1s ease 0.4s;animation:slide-in forwards 1.1s ease 0.4s;transform:translateY(-50%)}@-webkit-keyframes slide-in{from{transform:translate(100%, -50%)}to{transform:translate(var(--img-slide), -50%);opacity:1}}@keyframes slide-in{from{transform:translate(100%, -50%)}to{transform:translate(var(--img-slide), -50%);opacity:1}}header div.content{margin-left:var(--margin-sides);position:relative;display:flex;flex-direction:column;justify-content:center;height:100%;min-width:var(--main-width)}@-webkit-keyframes slide-up{from{transform:translateY(200px)}to{opacity:1;transform:translateY(0)}}@keyframes slide-up{from{transform:translateY(200px)}to{opacity:1;transform:translateY(0)}}header div.content h1{font-size:var(--title-size);opacity:0;-webkit-animation:slide-up 0.55s ease-out forwards 0s;animation:slide-up 0.55s ease-out forwards 0s}header div.content div.desc{margin:65px 0;opacity:0;-webkit-animation:slide-up 0.55s ease-out forwards .1s;animation:slide-up 0.55s ease-out forwards .1s}header div.content div.desc p{font-size:1rem;line-height:2rem;margin:18px 0}header div.content div.buttons{display:flex;flex-direction:row;opacity:0;-webkit-animation:slide-up 0.55s ease-out forwards .2s;animation:slide-up 0.55s ease-out forwards .2s}header div.content div.buttons a{margin-left:10px}header div.content div.buttons a:first-child{margin:0}header div.content div.buttons a button{text-transform:uppercase;background:white;border:4px solid indigo;font-weight:bold;font-size:0.8rem;padding:16px;transition:all .4s ease;display:flex;flex-direction:row;cursor:pointer}header div.content div.buttons a button span{display:block;font-size:inherit;font-size:1.1em;padding-left:6px;margin:0}header div.content div.buttons a button span.arrows{transform:rotate(0deg) translateY(-10%);transition:transform .4s ease}header div.content div.buttons a button:hover{background:indigo;color:white}header div.content div.buttons a button:hover span.arrows{transform:rotate(90deg) translateX(-25%) translateY(-20%)}@media only screen and (max-width: 900px){:root{--main-width: calc(100vw - var(--margin-sides) * 2)}header{min-height:500px}}@media only screen and (max-width: 500px){header div.content div.desc{margin:40px 0}}

section{width:100vw}section div.title{width:100%;background:indigo;margin:10px 0;display:flex;padding:var(--title-size) var(--margin-sides);color:white;align-items:center}section div.title h1{text-transform:uppercase;font-size:calc(var(--title-size) / 2)}section div.title img{height:4em;transition:opacity 0.3s}section div.title img:hover{opacity:0.75}section div.title span.spacer{font-size:1.5em;font-weight:bold;display:block;transform:translateY(-8%);padding:0 25px}section div.title a{font-size:1em;font-weight:lighter}section div.projects{--card-margin-multiplier: 2;width:100%;height:100%;padding:65px calc(var(--margin-sides) - 18px);display:flex;flex-wrap:wrap}section div.projects div.card{--one-third: calc(100% / var(--project-cols));--project-cols-minus-one: calc(var(--project-cols) - 1);--calculated-margin-normal: calc(
                18px * var(--project-cols-minus-one)
            );--calculated-margin: calc(
                var(--calculated-margin-normal) * var(--card-margin-multiplier)
            );width:calc(var(--one-third) - var(--calculated-margin));padding:50px 35px;margin:20px 18px;border-radius:1vh;border:1px solid lightgray;display:flex;flex-direction:column;align-items:center;text-align:center;position:relative}section div.projects div.card svg.pinned{height:30px;width:30px;position:absolute;top:20px;right:20px;transform:rotate(45deg);fill:black}section div.projects div.card div.info{height:90%}section div.projects div.card div.info img{height:100px}section div.projects div.card div.info h1{font-size:1.3em;margin-top:28px}section div.projects div.card div.info p{margin:28px 0;font-size:0.9em;line-height:1.75em}section div.projects div.card div.buttons{display:flex;margin-top:18px}section div.projects div.card div.buttons button{text-transform:uppercase;background:white;border:4px solid indigo;font-weight:bold;font-size:0.8rem;padding:16px;transition:all .4s ease;display:flex;flex-direction:row;cursor:pointer;font-size:0.56em;padding:12px;margin:0 5px}section div.projects div.card div.buttons button span{display:block;font-size:inherit;font-size:1.1em;padding-left:6px;margin:0}section div.projects div.card div.buttons button span.arrows{transform:rotate(0deg) translateY(-10%);transition:transform .4s ease}section div.projects div.card div.buttons button:hover{background:indigo;color:white}section div.projects div.card div.buttons button:hover span.arrows{transform:rotate(90deg) translateX(-25%) translateY(-20%)}section div.projects div.card div.buttons button:disabled{text-decoration:line-through}@media only screen and (min-width: 2560px){:root{--project-cols: 4}}@media only screen and (min-width: 3800px){:root{--project-cols: 5}}@media only screen and (min-width: 5760px){:root{--project-cols: 7}}@media only screen and (max-width: 1500px){:root{--project-cols: 2}}@media only screen and (max-width: 900px){:root{--project-cols: 1}}

